<template>
    <p-button
        label="Previous"
        icon="pi pi-chevron-left"
        class="p-mr-2 app-button p-button-sm"
        @click="previousCycle"
    />
    <p-button
        label="Next"
        icon="pi pi-chevron-right"
        iconPos="right"
        class="app-button p-button-sm"
        @click="nextCycle"
    />
</template>

<script>
import { computed } from "vue";
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();
        const selectedMonitorLine = computed(
            () => store.state.cycleMonitoring.selectedMonitorLine
        );

        return {
            selectedMonitorLine,
            nextCycle: () => store.dispatch("artCycle/nextCycle"),
            previousCycle: () => store.dispatch("artCycle/previsouCycle")
        };
    }
};
</script>

<style scoped>
.p-toolbar {
    background-color: transparent;
    border: none;
}
</style>
